import React from "react";
import { useState, useEffect } from "react";
import "./login.scss";
import {
  InputContainer,
  Button,
} from "../../components/globalComponents/globalComponent.jsx";
import { loginApi, getLogin } from "../../utils/api";
import { useNavigate } from "react-router-dom";
import { toast } from "material-react-toastify";

export const Login = () => {
  const [id, setId] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    (async () => {
      const isLogin = await getLogin();
      if (isLogin.data.isLoggedIn) {
        alert("  .   ");
        navigate(-1);
      }
    })();
  }, []);

  const handleIdChange = (e) => {
    setId(e.target.value);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  const handleLogin = async (e) => {
    const newLoginData = {
      accountId: id,
      password: password,
    };
    try {
      const login = await loginApi(newLoginData);
      navigate("/");
    } catch (err) {
      console.dir(err);
      toast.error(err.response.data.message);
      // alert(err.response.data.message);
    }
  };

  return (
    <div className="login-Container">
      <div className="login-Box">
        <img className="logo" src="/images/Logo.svg" />

        <div className="input-Box">
          <InputContainer
            type="text"
            placeholder=""
            value={id}
            onChange={handleIdChange}
          />
          <InputContainer
            type="password"
            placeholder=""
            value={password}
            onChange={handlePasswordChange}
          />
        </div>
        <Button color="" size="full" text="" onClick={handleLogin} />
      </div>

      <div className="p3 login-registerBox">
         ?
        <a className="p3" href="/register">
          
        </a>
      </div>
    </div>
  );
};
