export const USER_STATUS = {
  none: Symbol(),
  prev: Symbol(),
  current: Symbol(),
  getStr: function(userStatus) {
    if(userStatus === this.none) return '';
    if(userStatus === this.prev) return '  ';
    if(userStatus === this.current) return '  ';
  },
  getSymbol: function(userStatusStr) {
    if(userStatusStr === '') return this.none;
    if(userStatusStr === '  ') return this.prev;
    if(userStatusStr === '  ') return this.current;
  }
};

export const getUserStatus = (lectureSemester, userSemester, currentSemester) => {
  return userSemester ? ( lectureSemester === currentSemester ? (userSemester === lectureSemester ? USER_STATUS.current : USER_STATUS.prev) : USER_STATUS.prev ) : USER_STATUS.none;
};

export const CATEGORIES = [
  '0 ',
  { name: '', [USER_STATUS.none]: 0b11, [USER_STATUS.prev]: 0b11, [USER_STATUS.current]: 0b11 },
  { name: '  ', [USER_STATUS.none]: 0b01, [USER_STATUS.prev]: 0b11, [USER_STATUS.current]: 0b01},
  { name: ' Q&A ', [USER_STATUS.none]: 0b00, [USER_STATUS.prev]: 0b01, [USER_STATUS.current]: 0b11},
  { name: '  ', [USER_STATUS.none]: 0b00, [USER_STATUS.prev]: 0b00, [USER_STATUS.current]: 0b11},
];

export const getCategoryAuthority = (categoryId, userStatus) => {
  const write = (CATEGORIES[categoryId][userStatus] & 0b10) === 0b10;
  const read = (CATEGORIES[categoryId][userStatus] & 0b01) === 0b01;
  return {write, read};
}